				const slideY = this.PointY - PointY;
				if (slideY > uni.upx2px(120)) {
					this.canSend = false;
					this.voiceIconText = 'ÊùæÂºÄÊâãÊåá ÂèñÊ∂àÂèëÈÄÅ '
				} else if (slideY > uni.upx2px(60)) {
					this.canSend = true;
