			// H5 Á´ØÂ§ÑÁêÜ
			async uploadImageInH5(tempFilePath) {
				try {
					// 1. Â∞Ü Blob URL ËΩ¨Êç¢‰∏∫ File ÂØπË±°
					const response = await fetch(tempFilePath);
					const blob = await response.blob();
					const file = new File([blob], 'image.jpg', {
						type: blob.type
					});

					// 2. ÊûÑÈÄ† FormData
					const formData = new FormData();
					formData.append('file', file); // Êñá‰ª∂Â≠óÊÆµ
					formData.append('userId', '123'); // ÂÖ∂‰ªñÂèÇÊï∞

					// 3. ÂèëÈÄÅËØ∑Ê±ÇÔºà‰ΩøÁî® uni.request ÊàñÂéüÁîü fetchÔºâ
					const result = await this.sendFormDataRequest(formData);
					console.log('‰∏ä‰º†ÊàêÂäü:', result);
				} catch (err) {
					console.error('H5Á´Ø‰∏ä‰º†Â§±Ë¥•:', err);
				}
			},
			// Â∞èÁ®ãÂ∫èÁ´ØÂ§ÑÁêÜ
			uploadImageInMiniProgram(tempFilePath, type) {

				const uploadTasks = tempFilePath.map(file => {

					return new Promise((resolve, reject) => {
						uni.uploadFile({
							url: 'http://192.168.0.119:8080/upload/upload2Oss',
							filePath: file.tempFilePath,
							name: 'file', // ÂèÇÊï∞ÂêçÈúÄ‰∏éÂêéÁ´Ø‰∏ÄËá¥
							header: {
								'Authorization': "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE3NDEwNzQ4ODUsImV4cCI6MTc0MzY2Njg4NSwiZGF0YSI6eyJ1c2VyX3R5cGUiOiJ1c2VyIiwidXNlcl9pZCI6NDd9fQ.Ave2qlEte478fxGKlAD_Zbicmx-o27HG3LEnhHVoRLk"

							},
							formData: {
								client_id: this.jkId
							},
							success: (res) => {
								if (res.statusCode === 200) {
									try {
										const data = JSON.parse(res.data);

										this.sendMsg({
											contentType: type == 'image' ? 2 : 3,
											content: data.data.list[0],
											path: file.tempFilePath
										})

									} catch (e) {
										reject(new Error('Ëß£ÊûêÂìçÂ∫îÂ§±Ë¥•'));
									}
								} else {
									reject(new Error(`‰∏ä‰º†Â§±Ë¥•ÔºåÁä∂ÊÄÅÁ†ÅÔºö${res.statusCode}`));
								}
							},
							fail: (err) => {
								reject(err);
							}
						});
					});
				});

				// ÂêàÂπ∂ÊâÄÊúâÁªìÊûú
				Promise.all(uploadTasks)
					.then(results => {
						const allList = results.flat(); // ÂêàÂπ∂ÊàêÂÆåÊï¥Ë∑ØÂæÑÊï∞ÁªÑ
						console.log('‰∏ä‰º†ÊàêÂäüÔºåÊñá‰ª∂ÂàóË°®Ôºö', allList);



					})
					.catch(error => {
						console.error('‰∏ä‰º†Âá∫ÈîôÔºö', error);
						uni.showToast({
							title: '‰∏ä‰º†Â§±Ë¥•',
							icon: 'none'
						});
					});

			},
